import React, { useEffect, useState } from 'react';
import { Col, Row } from 'antd';
import { histories } from '../home/index';
import Overview from './Overview';
import Content from './Content';
import Chapter from './Chapter';
import Comment from './Comment';
import ListMangaSide from '../../../components/manga/ListMangaSide';
import { useParams } from 'react-router-dom';
import { getMangaDetail } from '../../../services/Guest/index';

const DetailManga = () => {
  const [manga, setManga] = useState({});
  const { name } = useParams();

  const fetchDetailManga = async (id) => {
    const data = await getMangaDetail(id);
    if (data && data.status === 200) {
      setManga(data.data);
    }
  };

  useEffect(() => {
    if (name) {
      fetchDetailManga(name);
    }
  }, [name]);

  return (
    <Row style={{ justifyContent: 'center' }}>
      <Col span={18}>
        <Row>
          <Col span={16}>
            <Overview manga={manga} />
            <Content content={manga?.description} />
            <Chapter chapters={manga?.chapters} nameManga={manga?.slug} />
            <Comment comments={manga?.comments} />
          </Col>
          <Col span={8}>
            <ListMangaSide
              listManga={histories}
              title={'Đề xuất'}
              isDate={false}
            />
            <ListMangaSide listManga={histories} title={'Lịch sử'} />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default DetailManga;
